html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 20px;
  background-color: #cecece;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
}
@media only screen and (min-width: 1000px) {
  body {
    padding: 1.5rem;
  }
}

main {
  padding: 20px;
  background-color: #ffffff;
  border-radius: 15px;
  max-width: 1200px;
  margin: 0 auto;
}
@media only screen and (min-width: 1000px) {
  main {
    display: flex;
    padding: 1.5rem;
  }
}

@media only screen and (min-width: 1000px) {
  .content {
    flex: 3;
  }
}

@media only screen and (min-width: 1000px) {
  aside {
    flex: 1;
  }
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}